* {
  box-sizing: border-box;
}
.App {
  display: flex;
  width: 100%;
  background-color: rgb(9, 10, 22);
  color: white;
  height: 100vh;
  overflow: hidden;
}

.sign-in {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  height: 200px;
  width: 400px;
  background-color: cornflowerblue;
  border-radius: 6px;
  gap: 15px;
}
.sign-in .welcome {
  color: black;
  font-size: 20px;
  font-weight: 500;
}
.sign-in button {
  height: 40px;
  border: none;
  background-color: rgb(101, 101, 230);
  color: white;
  padding: 5px 8px;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s ease;
}
.sign-in button:hover {
  background-color: rgb(69, 69, 179);
}

.chat-room {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.head {
  height: 50px;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  border-bottom: 1px solid rgb(161, 159, 159);
}
.head span {
  font-weight: bold;
  color: rgb(31, 213, 219);
  font-size: 18px;
}
.head button {
  border: none;
  background-color: rgb(192, 5, 5);
  color: white;
  padding: 5px 8px;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s ease;
  height: 30px;
  display: flex;
  align-items: center;
}
.head button:hover {
  background-color: rgb(153, 12, 12);
}

#messages-scroll {
  width: 100%;
  height: calc(100vh - 100px);

  margin: 5px 0;
}
.messages-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
#messages-scroll .message {
  display: flex;
  align-items: center;
  width: 100%;
  height: 35px;
  padding: 0 8px;
  overflow: hidden;
  gap: 10px;
}
#messages-scroll .message.recieved {
  direction: rtl;
}
#messages-scroll .message img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
}
#messages-scroll .message .text {
  height: 35px;
  padding: 5px 10px;
  border-radius: 8px;
  background-color: rgba(59, 59, 59, 0.7);
}
#messages-scroll .message.recieved .text {
  background-color: rgba(161, 70, 70, 0.7);
}

.input-form {
  width: 100%;
  height: 50px;
  display: flex;
  padding-bottom: 5px;
}
.input-form input {
  width: 100%;
  padding-right: 80px;
  border-radius: 0;
}
.input-form input:focus {
  box-shadow: none;
}

.input-form button {
  width: 80px;
  border: none;
  background-color: rgb(101, 101, 230);
  color: white;
  padding: 5px 8px;
  /* border-radius: 0 4px; */
  cursor: pointer;
  transition: 0.3s ease;
}
.input-form button:hover {
  background-color: rgb(69, 69, 179);
}
